import React, {useRef, useEffect} from 'react';
import {gsap, TimelineLite} from 'gsap';
import './App.css';
import './Responsive.css';



function App() {
  
  let anime = useRef(null);
  
  let tl = new TimelineLite();

  useEffect(() => {
   

    gsap.to(anime, 0, {css: {visibility: 'visible'}})
    
    
    tl.to('.block2', 1.5, {width: "28%", ease: "power4.inOut", delay: 4.5}, 0.1)
    tl.to('.block1', 1.5, {width: "72%", ease: "power4.inOut", delay: 4.5}, 0.1)
    tl.to('.visual', 1.5, {right: "6.5vw", ease: "power4.inOut", delay: 4}, 0.1)
    tl.from('.anchor1', 1.5, {opacity: 0, display: "none", delay: 7}, 0.1)
    tl.from('.anchor2', 1.5, {opacity: 0, display: "none", delay: 7}, 0.1)
    tl.from('.head1 div', 1.3, {clipPath: 'inset(0 0 100% 0)', y: 150, ease: "power4.out", stagger: 0.08, delay: 6}, 0.1)
    tl.from('.intros div', 1.2, {clipPath: 'inset(0 0 100% 0)', y: 50, ease: "power4.out", stagger: 0.08, delay: 6}, 0.1)
    tl.from('.signup div', 1.2, {clipPath: 'inset(0 0 100% 0)', y: 50, ease: "power4.out", stagger: 0.08, delay: 6}, 0.1)
    tl.from('.login', 1.2, {clipPath: 'inset(0 0 100% 0)', y: 50, ease: "power4.out", delay: 6}, 0.1)
    tl.from('.svg1', 1.2, {clipPath: 'inset(0 100% 0 0)', x: 30, ease: "power4.out", transform: "skewX(10deg)", delay: 6}, 0.1)
    tl.from('.abss', 6, {ease: "power4.out", transform: "skewY(5deg)", yoyo: true, repeat: -1, opacity: 0, delay: 6}, 0.1)
    tl.from('.hi', 2, {ease: "power4.out", opacity: 0, delay: 6}, 0.1)
    tl.from('.bullet1', 2, {ease: "power4.out", opacity: 0, delay: 6}, 0.1)
    tl.from('.bullet2', 2, {ease: "power4.out", opacity: 0, delay: 6}, 0.1)
    tl.from('.copy', 2, {ease: "power4.out", opacity: 0, delay: 6}, 0.1)
    tl.to('.underline', 1.2, {width: "21.8%", ease: "power4.inOut", delay: 7}, 0.1)
  })
     

  const visualizer = 'https://res.cloudinary.com/dznkentev/image/upload/v1615112552/Tushar%20Project%20Assets/Audio_Visualizer.png';
  const abstract_bg = 'https://res.cloudinary.com/dznkentev/image/upload/v1615113588/Tushar%20Project%20Assets/Abstract%20Backg.png';
  const menu = 'https://res.cloudinary.com/dznkentev/image/upload/v1615113950/Tushar%20Project%20Assets/Hamburger%20Menu.svg';
  const anchor1 = 'https://res.cloudinary.com/dznkentev/image/upload/v1615114856/Tushar%20Project%20Assets/Page_Anchor_1_vvt4rk.png';
  const anchor2 = 'https://res.cloudinary.com/dznkentev/image/upload/v1615114857/Tushar%20Project%20Assets/Page_Anchor_2_uirkcx.png';
  const wave = 'https://res.cloudinary.com/dznkentev/image/upload/v1615111143/Tushar%20Project%20Assets/Voice%20Logo.png';

  return (
    <div className="App" ref={el => anime = el}>

      <div className="warn-block">
        <h1>Sorry for the inconvenience</h1>
        <p>This application is not supported on mobile device</p>
        <p>Please switch to desktop or use the mobile app.</p>
      </div>
     
      <img src={visualizer} alt="audio_visualizer" className="visual"/>
      <img src={abstract_bg} alt="abstract" className="abss"/>
     <div className="block1">

     <nav className="bar1">
        <img src={menu} alt="menu" className="svg1"/>
        <p className="signup"><div>Create</div> <div>an</div> <div>account</div></p>
        
      </nav>

      <div className="bullet1"></div>
      <div className="bullet2"></div>

      <div className="bottom-nav">
        <p className="copy">&copy;All Rights Reserved. 2021.</p>
      </div>

      <h1 className="head1">
        <div>P</div><div>R</div><div>I</div><div>M</div><div>A</div>
      </h1>

      <hr className="underline"/>

      <h5 className="intros">
        <div>An AI solution to find your favorite location.</div>
        <div>we are listening.</div>
      </h5>

      <div className="hi">
         <img src={wave} alt="wave" className="wave"/>
         <p className="sayer">Say 'Hi.'</p>
         </div>
     </div>
     <div className="block2">
       
       <nav className="bar2">
         <p className="login">Login</p>
       </nav>

       <img src={anchor1} alt="anchor1" className="anchor1"/>
       <img src={anchor2} alt="anchor2" className="anchor2"/>

      
     </div>
    </div>
  );
}

export default App;
